import { useState, useEffect } from "react";
import { withRouter, useParams, useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import * as yup from "yup";

import { yupResolver } from "@hookform/resolvers/yup";
import LiveSearch from "../utils/LiveSearch";

//import DatePicker  from 'react-datepicker';
//import "react-datepicker/dist/react-datepicker.css";
//import addDays from 'date-fns/addDays'

import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";

const schema = yup.object({});

const PmUploadControlEdit = () => {
  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
    clearErrors,
  } = useForm({
    resolver: yupResolver(schema),
  });

  let { id } = useParams();

  let history = useHistory();
  const [serverErrors, setServerErrors] = useState([]);
  const [entity, setEntity] = useState({});
  const [state, setState] = useState({});
  const [key, setKey] = useState("page1");
  const [me, setMe] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [lightTheme, setLightTheme] = useState(true);

  const onSubmit = (data, event) => {
    setDisabled(true);
    event.preventDefault();
    console.log(me);
    if (me) {
      axios
        .get(`/pmUploadControls/pmUploadFile/${me}`)
        .then((response) => {
          if(response.data){
            window.open(
              `${process.env.REACT_APP_BASE_URL}/files/${response.data}`
            );
            history.push("/pmUploadControls");
          }
          setDisabled(false);
           
          
        })
        .catch((error) => {
          //console.log(error.response.data);
          //console.log(error.response.status);
          //console.log(error.response.headers);
          setServerErrors(error.response.data);
        });
    }
  };

  const onError = (errors, e) => console.log(errors, e);

  //All foreign keys.
  // XXXXXX Add search fields and adjust the values as necessary

  //Callback for child components (Foreign Keys)
  const callback = (childData) => {
    //console.log("Parent Callback");
    setEntity((prev) => ({ ...prev, [childData.fk]: childData.entity }));
    setValue(childData.fk, childData.entity);
    //console.log(errors);
    clearErrors(childData.fk);
  };

  const errorCallback = (err) => {
    //console.log(err);
    setServerErrors(err);
  };

  const handleInputChange = (e) => {
    //console.log(e.target.value);
    setMe(e.target.value);
  };

  return (
    <div className="max-w-xl mx-auto ">
      <div className="w-full w-3/4  mx-auto ">
        <div
          className={lightTheme ? "theme-light" : "theme-dark"}
          style={disabled ? { pointerEvents: "none", opacity: "0.4" } : {}}
        >
          <form onSubmit={handleSubmit(onSubmit, onError)}>
            <h1>{id === "new" ? "Add" : "Edit"} Pm Upload File Generation </h1>
            <div className="text-red-500">{serverErrors}</div>
            <Tabs
              id="PmUploadControlEdit"
              activeKey={key}
              onSelect={(k) => setKey(k)}
              className="mb-3"
            >
              <Tab eventKey="page1" title="Page 1" className="h-120">
                <div className="grid grid-cols-2 gap-0">
                  <div>
                    <label>Pm Month</label>
                    <input
                      type="text"
                      name="me"
                      onChange={handleInputChange}
                      placeholder="mmyyyy"
                      className="form-control py-0"
                    />
                    <div className="text-red-500">
                      {errors.batchNo?.message}
                    </div>
                  </div>

                  <div>
                    <label>Pm Section Code</label>
                    <input
                      type="text"
                      name="pmSectionCode"
                      {...register("pmSectionCode")}
                      className="form-control py-0"
                    />
                    <div className="text-red-500">
                      {errors.pmSectionCode?.message}
                    </div>
                  </div>
                </div>
              </Tab>

              <Tab eventKey="page2" title="Page 2" className="h-120">
                <div className="grid grid-cols-2 gap-0">
                  <p>Add some fields here or delete this tab.</p>
                </div>
              </Tab>

              <Tab eventKey="help" title="Help">
                <h1>Help</h1>
                <ul className="list-disc">
                  <li>Point 1</li>
                  <li>Point 2</li>
                </ul>
              </Tab>
            </Tabs>

            <div className="px-4">
              <button type="submit">Save</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default withRouter(PmUploadControlEdit);
